import React, { useState, useEffect } from 'react';
import { Layout, ConfigProvider, Progress } from 'antd';
import './App.css';
import WelcomePage from './WelcomePage';
import QuestionCard from './QuestionCard';
import Result from './Result';
import Confetti from 'react-confetti';
import { useWindowSize } from 'react-use';

const { Header, Content } = Layout;

interface Question {
  question: string;
  options: string[];
  correctIndex: number;
}

// Utility function to shuffle an array without mutating the original
const shuffleArray = (array: any[]) => {
  const newArray = array.slice(); // Make a shallow copy
  for (let i = newArray.length - 1; i > 0; i--) {
    const j = Math.floor(Math.random() * (i + 1));
    [newArray[i], newArray[j]] = [newArray[j], newArray[i]];
  }
  return newArray;
};

const getRandomQuestions = (questions: Question[]) => {
  // Select 4 random questions
  const selectedQuestions = shuffleArray(questions).slice(0, 4);

  // Shuffle answers for each selected question
  return selectedQuestions.map((question) => {
    // Pair each option with its original index
    const optionsWithIndex = question.options.map((option: any, index: any) => ({
      text: option,
      originalIndex: index,
    }));

    // Shuffle the options
    const shuffledOptionsWithIndex = shuffleArray(optionsWithIndex);

    // Find the new index of the correct answer
    const newCorrectIndex = shuffledOptionsWithIndex.findIndex(
      (option) => option.originalIndex === question.correctIndex
    );

    // Extract the text of options
    const shuffledOptions = shuffledOptionsWithIndex.map((option) => option.text);

    return {
      ...question,
      options: shuffledOptions,
      correctIndex: newCorrectIndex,
    };
  });
};

const App: React.FC = () => {
  const allQuestions: Question[] = [
    {
      question: "How many years ago was the A-Team founded?",
      options: ["12", "8", "4", "15"],
      correctIndex: 0,
    },
    {
      question: "What is the maximum period interns are employed for? (months)",
      options: ["18", "12", "6", "24"],
      correctIndex: 0,
    },
    {
      question: "What does the abbreviation FLP stand for?",
      options: ["Future Leaders Programme", "Fantastic Leaders Poland", "First Leaders Performance", "Former Lame People"],
      correctIndex: 0,
    },
    {
      question: "How long does the FLP last?",
      options: ["2 years", "1.5 years", "3 years", "1 year"],
      correctIndex: 0,
    },
    {
      question: "Statistically, how many people who completed FLP continued to work permanently at GSK?",
      options: ["100%", "80%", "76%", "49%"],
      correctIndex: 0,
    },
    {
      question: "How many A-Team meetings have been held so far?",
      options: ["600", "10", "150", "320"],
      correctIndex: 0,
    },
    {
      question: "What is the full form of STEM?",
      options: ["Science Technology Engineering Mathematics", "Science Training Exploration Module", "Social Team encouraging musicians", "Super Talented Engaged Masters"],
      correctIndex: 0,
    },
    {
      question: "Who is the A-Team for?",
      options: ["Interns and employees", "Only interns", "Only employees", "Managers"],
      correctIndex: 0,
    },
    {
      question: "Since which year has FLP been operating in Poland?",
      options: ["2021", "2019", "2024", "2012"],
      correctIndex: 0,
    },
  ];

  const [questions, setQuestions] = useState<Question[]>([]);
  const [currentQuestionIndex, setCurrentQuestionIndex] = useState(0);
  const [score, setScore] = useState(0);
  const [quizFinished, setQuizFinished] = useState(false);
  const [quizStarted, setQuizStarted] = useState(false);
  const [timeLeft, setTimeLeft] = useState(15);
  const [isAnswerShown, setIsAnswerShown] = useState(false);

  const handleStartQuiz = () => {
    setQuestions(getRandomQuestions(allQuestions));
    setCurrentQuestionIndex(0);
    setScore(0);
    setQuizStarted(true);
    setQuizFinished(false);
    setTimeLeft(15);
    setIsAnswerShown(false);
  };

  const handleAnswer = (isCorrect: boolean) => {
    if (isCorrect) {
      setScore((prevScore) => prevScore + 1);
    }
    setIsAnswerShown(true);
  };

  const goToNextQuestion = () => {
    const nextIndex = currentQuestionIndex + 1;
    if (nextIndex < questions.length) {
      setCurrentQuestionIndex(nextIndex);
      setTimeLeft(15);
      setIsAnswerShown(false);
    } else {
      setQuizFinished(true);
    }
  };

  useEffect(() => {
    let timer: NodeJS.Timeout;
    if (quizStarted && !quizFinished && !isAnswerShown) {
      if (timeLeft > 0) {
        timer = setTimeout(() => setTimeLeft(timeLeft - 1), 1000);
      } else {
        handleAnswer(false);
      }
    }
    return () => clearTimeout(timer);
  }, [timeLeft, quizStarted, quizFinished, isAnswerShown]);

  const { width, height } = useWindowSize();
  const shouldShowConfetti = quizFinished && score >= questions.length - 1;

  return (
    <ConfigProvider
      theme={{
        token: {
          colorPrimary: '#fa8c16',
        },
      }}
    >
      {shouldShowConfetti && <Confetti width={width} height={height} />}
      {!quizStarted ? (
        <WelcomePage onStart={handleStartQuiz} />
      ) : (
        <Layout style={{ minHeight: '100vh' }}>
          {!quizFinished && (
            <Header
              style={{
                backgroundColor: '#fff',
                display: 'flex',
                justifyContent: 'center',
                alignItems: 'center',
                borderBottomLeftRadius: '20px',
                borderBottomRightRadius: '20px',
                position: 'relative',
                zIndex: 2,
              }}
            >
              <img
                src="https://upload.wikimedia.org/wikipedia/en/thumb/3/32/GSK_logo_2022.svg/1200px-GSK_logo_2022.svg.png"
                alt="Logo"
                style={{ height: '30px' }}
              />
            </Header>
          )}
          {!quizFinished && quizStarted && (
            <div style={{ padding: '0 15px', marginTop: '10px' }}>
              <Progress
                percent={(timeLeft / 15) * 100}
                showInfo={false}
                strokeColor="#fa8c16"
                status="active"
              />
            </div>
          )}
          <Content
            style={{
              padding: '10px',
              display: 'flex',
              justifyContent: 'center',
              alignItems: 'center',
              height: '100vh',
              background: quizFinished
                ? 'linear-gradient(315deg, #ff9a76 3%, #ff6f42 38%, #ff3e1d 68%, #ffbd80 98%)'
                : 'none',
              animation: quizFinished ? 'gradient 15s ease infinite' : 'none',
            }}
          >
            {questions.length > 0 ? (
              !quizFinished ? (
                <QuestionCard
                  question={questions[currentQuestionIndex]}
                  questionNumber={currentQuestionIndex + 1}
                  totalQuestions={questions.length}
                  onAnswer={handleAnswer}
                  onNext={goToNextQuestion}
                  isAnswerShown={isAnswerShown}
                />
              ) : (
                <Result score={score} totalQuestions={questions.length} />
              )
            ) : (
              <p>Loading questions...</p>
            )}
          </Content>
        </Layout>
      )}
    </ConfigProvider>
  );
};

export default App;
